.settings {
    display: flex;
    position: static;
    cursor: pointer;
    font-size: 28px;
    color: rgba(255, 255, 255, 0.2);
}
.colorCan{
    z-index: 3;
    display: flex;
    position: fixed;
    align-items: center;
    right: 2vw;
    top: 8vh;
    min-height: 57px;
}
.rolling {
    color: var(--color);
    animation: roll 0.8s infinite ease-in-out;
}

@keyframes roll {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}
.colors{
    background: var(--navbar-background);
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    align-items: center;
    margin-right: 15px;
    border-radius: 10px;
    height: 0;
}
.showColors{
    padding: 3px;
    height: 50px;
}

.Color{
    width: 0;
    cursor: pointer;
    height: 0;
    margin: 7px;
    border-radius: 4px;
}
.showColor{
    width: 25px;
    height: 25px;
}
@media screen and (max-width: 425px){
    .colorCan{
        right: 6vw;
    }
}